import Vue from 'vue';
import Router from 'vue-router';
import Homepage from '@/components/Homepage';
import ShipmentResult from '@/components/ShipmentResult';
import ShipmentSearch from '@/components/ShipmentSearch';

Vue.use(Router);

const router = new Router({
  mode: 'history',
  routes: [
    {
      name: 'Homepage',
      path: '/',
      component: Homepage,
    },
    {
      name: 'Result',
      path: '/result',
      component: ShipmentResult,
    },
    {
      name: 'Search',
      path: '/search',
      component: ShipmentSearch,
    },
  ],
  scrollBehavior(to, from, savedPosition) {
    return { x: 0, y: 0 };
  },
});

export default router;



