<template>
  <div id="app">
    <div class="background">
      <MainMenu />
        <router-view />
        <vue-progress-bar />
      <MainFooter />
    </div>
  </div>
</template>

<script>
import './assets/bootstrap-reboot.css';
import './assets/bootstrap-grid.css';
import './assets/Fonts.css';
import './assets/buttons.css';

import MainMenu from './components/MainMenu';
import MainFooter from './components/MainFooter';
export default {
  name: 'App',
  components: {
    MainMenu,
    MainFooter,
  },
};
</script>

<style>
* {
  box-sizing: border-box;
}

a {
  font-weight: inherit;
  font-size: inherit;
  color: inherit;
  text-decoration: none;
}

#app {
  font-family: 'Circe', 'Roboto', 'Arial', sans-serif;
  min-height: 100vh;
  padding: 20px 20px 0;
  position: relative;
  background-color: #f7f7f7;
  display: flex;
}

.background {
  background-color: #fff;
  display: flex;
  flex-flow: row wrap;
  justify-content: space-between;
  width: 100%;
}

.background > div {
  width: 100%;
}

.v--modal-overlay {
  padding: 105px 0;
  display: flex;
  justify-content: center;
  align-items: center;
}

.v--modal-box {
  position: static !important;
  overflow: visible !important;
  height: auto !important;
}

.common-button,
.common-button-white {
  background-color: #fff;
  padding: 20px 51px;
  border-radius: 32px;
  outline: 0;
  border: 0;
  cursor: pointer;
  font-size: 16px;
  font-weight: bold;
  position: relative;
  z-index: 1;
  display: inline-block;
}

.common-button:before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  border-radius: 32px;
  background: #425fff;
  z-index: -1;
  opacity: 0;
  transform: scale3d(0.6, 0.6, 1);
  transition: transform 0.3s, opacity 0.3s;
  transition-timing-function: cubic-bezier(0.75, 0, 0.125, 1);
}

.common-button {
  background-image: linear-gradient(351deg, #274bff, #081cff);
  color: #fff;
  transition: all 0.3s ease-in-out;
}

.common-button:hover:before {
  transform: scale3d(1, 1, 1);
  opacity: 1;
}

.common-button-white {
  background-image: white;
  color: rgba(0, 0, 0, 0.2);
  border: solid 1px rgba(0, 0, 0, 0.2);
  transition: all 0.3s ease-in-out;
}

.common-button-white:hover {
  background-image: linear-gradient(351deg, #274bff, #081cff);
  color: #fff;
}

.common-button:hover {
  color: #fff;
  text-decoration: none;
}

.sub-title {
  font-size: 20px;
  font-weight: 300;
  letter-spacing: 0.5px;
  color: #6e6e6e;
}

.title {
  font-size: 65px;
  font-weight: 300;
  letter-spacing: 0.7px;
  line-height: 60px;
  margin-bottom: 10px;
}
</style>



