<template>
  <div id="shipmentresult">
    <div class="shipment-result container d-flex flex-column align-items-center">
      <h1 class="title">
        Маршруты
      </h1>
      <h2 class="sub-title">
        Мы отобрали для вас самые подходящие варианты по результатам поиска
      </h2>
      <div class="shipment-result__info col-12">
        <div class="shipment-result__info-top col-10">
          <div class="d-flex align-items-stretch position-relative">
            <div class="shipment-result__col shipment-result__col_big col-6">
              <div class="shipment-result__row d-flex">
                <h3 class="shipment-result__title">
                  Shanghai — Vladivostok:
                </h3>
                <p class="shipment-result__desc d-flex">
                  680$ (0% НДС), Фрахт, 6-10 дней
                </p>
              </div>
              <div class="shipment-result__row d-flex">
                <h3 class="shipment-result__title">
                  Vladivostok — Novosibirsk:
                </h3>
                <p class="shipment-result__desc">
                  75.000 Р. (18% НДС), Ж.Д, 14-17 дней
                </p>
              </div>
              <div class="shipment-result__row d-flex">
                <h3 class="shipment-result__title">
                  Срок выдачи порожнего контейнера:
                </h3>
                <p class="shipment-result__desc">
                  14 дней
                </p>
              </div>
            </div>
            <div class="shipment-result__col col-3 shipment-result__col_little d-flex flex-wrap align-content-center justify-content-center">
              <h3 class="shipment-result__total">Итого:</h3>
              <p class="shipment-result__total-count">143 000 Р</p>
              <span class="shipment-result__last-date">Актуально до 25.02.18</span>
            </div>
            <div class="shipment-result__col col-3 shipment-result__col_middle">
              <div class="receive-container">Выдача контейнера</div>
              <div class="receive-logo maersk"></div>
            </div>
            <div class="shipment-result__waves">
            </div>
          </div>
        </div>
        <div class="shipment-result__info-big-row d-flex align-items-center justify-content-center">
          <div class="shipment-result__info-options">
            <!-- <h2 class="shipment-result__info-options-title title-left">Ещё варианты</h2>
            <div class="shipment-result__info-options-circles circles-left">
              <div class="info-options-circle shipment-result__info-options-circles_big"></div>
              <div class="info-options-circle shipment-result__info-options-circles_middle"></div>
              <div class="info-options-circle shipment-result__info-options-circles_small"></div>
            </div> -->
          </div>
          <div class="shipment-result__info-top_width col-10">
            <div class="shipment-result__info-bottom d-flex align-items-center justify-content-between">
              <div class="shipment-result__city">
                <div class="shipment-result__city-name">Shanghai, CN</div>
                <div class="shipment-result__transport ship-img"></div>
              </div>
              <div class="shipment-result__city">
                <div class="shipment-result__city-name">Vladivostok, RU</div>
                <div class="shipment-result__transport car-img"></div>
              </div>
              <div class="shipment-result__city">
                <div class="shipment-result__city-name">Novosibirsk, RU</div>
                <div class="shipment-result__transport train-img"></div>
              </div>
            </div>
          </div>
          <!-- <div class="shipment-result__info-options">
            <div class="shipment-result__info-options-circles circles-right">
              <div class="info-options-circle shipment-result__info-options-circles_big"></div>
              <div class="info-options-circle shipment-result__info-options-circles_middle"></div>
              <div class="info-options-circle shipment-result__info-options-circles_small"></div>
            </div>
            <h2 class="shipment-result__info-options-title title-right">Новый поиск</h2>
          </div> -->
        </div>
        <div class="new__search">
          <router-link :to="{name: 'Result'}" tag="a" class="button button--wapasha d-flex align-items-center justify-content-center radius-50 shipment-result__link">Новый поиск</router-link>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import '../assets/ShipmentResult.css';
import MainMenu from './MainMenu';
export default {
  name: 'Result',
  beforeRouteLeave(to, from, next) {
    this.$Progress.start();
    setTimeout(() => {
      this.$Progress.finish();
      next();
    }, 2000);
  },
  components: {
    MainMenu,
  },
};
</script>



