<template>
  <div id="mainMenu">
    <div class="menu container">
      <div class="menu__nav col-md-12 d-flex justify-content-between">
        <nav class="d-flex col-md-4 align-items-center">
          <a href="#" class="menu__route">Перевозчикам</a>
          <a href="#" class="menu__route">Пользователям</a>
          <a href="#" class="menu__route">Премиум</a>
        </nav>
        <router-link :to="{name: 'Homepage'}" tag="a" class="logo"></router-link>
        <div class="col-md-4 d-flex align-items-center justify-content-end">
          <span @click="$modal.show('login')" class="menu__actions pointer menu__sing-in">Вход</span>
          <span @click="$modal.show('registration')"
                class="menu__actions pointer button button--wapasha d-flex align-items-center justify-content-center padding-stand">Регистрация</span>
          <div class="menu__change-lang menu__actions">
            <div class="select">
              <input id="rus" class="d-none" type="radio" name="action" value="rus" checked>
              <label for="rus" class="rus lang-logo"></label>
              <input id="en" class="d-none" type="radio" name="action" value="en">
              <label for="en" class="en lang-logo"></label>
            </div>
          </div>
        </div>
      </div>
    </div>

    <modal name="registration">
      <div class="modal">
        <div class="modal-content">
          <div class="modal-container">
            <div class="modal-header">
              <h5 class="modal-title">Новый аккаунт</h5>
            </div>
            <div class="modal-body">
              <div class="modal-form">
                <form>
                  <div class="controls-container">
                    <CombinedInput
                      :validationRules="{ required: true, alpha_num: true}"
                      type="text"
                      labelText="Логин"
                      name="login"
                      placeholder="Ваш логин"/>
                    <CombinedInput
                      :validationRules="{ required: true, email: true}"
                      type="text"
                      labelText="Email"
                      name="email"
                      placeholder="example@mail.com"/>
                    <CombinedInput type="text" labelText="Пароль" name="password" placeholder="•••••••••••••"/>
                  </div>
                  <p class="modal-terms">* Нажимая «Создать» Вы соглашаетесь с условиями пользовательского
                    соглашения</p>
                  <span class="common-button submit">Создать</span>
                </form>
              </div>
            </div>
          </div>
          <div class="modal-close" @click="$modal.hide('registration')">
            <div class="modal-button">
              <span class="cross first"/>
              <span class="cross second"/>
            </div>
          </div>
        </div>
      </div>
    </modal>

    <modal name="login">
      <div class="modal">
        <div class="modal-content">
          <div class="modal-container">
            <div class="modal-header">
              <h5 class="modal-title">Вход в аккаунт</h5>
            </div>
            <div class="modal-body">
              <slot name="modal-add-description"></slot>
              <div class="modal-form">
                <form>
                  <div class="controls-container">
                    <CombinedInput
                      :validationRules="{ required: true, alpha_num: true}"
                      type="text" labelText="Логин" name="login" placeholder="Ваш логин"/>
                    <CombinedInput type="text" labelText="Пароль" name="password" placeholder="•••••••••••••"/>
                  </div>
                  <div class="modal-controls">
                    <label class="checkbox">
                      <input type="checkbox" name="remember"/>
                      <span class="checkbox__checker"/>
                      <span class="text">Запомнить</span>
                    </label>
                    <span class="recovery pointer" @click="($modal.hide('login'), $modal.show('recovery'))">Восстановление пароля</span>
                  </div>
                  <div class="buttons-container">
                    <span class="common-button submit margin-55">Создать</span>
                    <span @click="($modal.hide('login'), $modal.show('registration'))"
                          class="common-button-white submit margin-55">Регистрация</span>
                  </div>
                </form>
              </div>
            </div>
          </div>
          <div class="modal-close" @click="$modal.hide('login')">
            <div class="modal-button">
              <span class="cross first"/>
              <span class="cross second"/>
            </div>
          </div>
        </div>
      </div>
    </modal>

    <modal name="recovery">
      <div class="modal">
        <div class="modal-content">
          <div class="modal-container">
            <div class="modal-header">
              <h5 class="modal-title">Восстановление пароля</h5>
            </div>
            <div class="modal-body">
              <div class="modal-add-description">
                Забыли пароль? Напишите Ваш Email и мы вышлем инструкцию по восстановлению на указанный адрес
              </div>
              <div class="modal-form">
                <form>
                  <div class="controls-container">
                    <CombinedInput
                      :validationRules="{ required: true, email: true}"
                      type="text" labelText="Email" name="email" placeholder="example@mail.com"/>
                  </div>
                  <div class="back">
                    <span @click="($modal.hide('recovery'), $modal.show('login'))">Вернуться ко входу в аккаунт</span>
                  </div>
                  <span class="common-button submit margin-55">Восстановить</span>
                </form>
              </div>
            </div>
          </div>
          <div class="modal-close" @click="$modal.hide('recovery')">
            <div class="modal-button">
              <span class="cross first"/>
              <span class="cross second"/>
            </div>
          </div>
        </div>
      </div>
    </modal>
  </div>
</template>

<script>
  import CombinedInput from './CombinedInput';

  import '../assets/Menu.css';
  import '../assets/Modal.css';

  export default {
    name: 'MainMenu',
    components: {
      CombinedInput,
    },
  };
</script>



